.page-footer {
  background-color: $background-color-primary;
  color: $color-primary;
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 22px;

  @media (min-width: $tablet-width) {
    padding-top: 27px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 180px;
    padding-right: 180px;
  }
}

.page-footer__navigation {
  width: 41%;

  @media (min-width: $tablet-width) {
    display: flex;
    width: 52%;
  }
}

.page-footer__contacts {
  width: 53%;

  @media (min-width: $tablet-width) {
    width: 48%;
  }

  @media (min-width: $desktop-width) {
    padding-left: 17px;
    box-sizing: border-box;
  }
}

.page-footer-nav__link {
  line-height: 34px;
}

.page-footer__logo {
  display: inline-block;
  margin-bottom: 19px;

  @media (min-width: $tablet-width) {
    margin-top: 11px;
    margin-right: 71px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 4px;
    margin-right: 73px;
  }
}

.page-footer__phone {
  @include font(12px, 24px);
  display: block;
  margin-top: 1px;
  margin-left: 2px;

  @media (min-width: $tablet-width) {
    margin-top: 4px;
  }
}

.page-footer__address {
  @include font(12px, 15px);
  margin: 0 0 9px;

  @media (min-width: $tablet-width) {
    @include font(14px, 14px);
    margin-top: 8px;
  }
}

.page-footer__button {
  font-family: $font-family-primary;
  text-transform: none;
  color: $color-secondary;
  display: block;
  text-align: left;

  @media (min-width: $tablet-width) {
    @include font(12px, 14px);
    display: inline-block;
    margin-left: 3px;
  }
}

.page-footer__button-title {
  text-decoration: underline;

  &:hover {
    text-decoration: none;
    cursor: pointer;
  }

  &:active,
  &:focus {
    text-decoration: none;
  }
}

.page-footer__copyright {
  @include font(10px, 12px);

  @media (min-width: $tablet-width) {
    @include font(12px, 24px);
  }
}

.page-footer__copyright-year {
  display: block;

  @media (min-width: $tablet-width) {
    display: inline-block;
  }
}

.page-footer__user-nav-link {
  @media (min-width: $tablet-width) {
    margin-left: 24px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 43px;
  }
}
