.advantages {
  background-color: $background-color-primary-light;
  padding-bottom: 60px;
  padding-top: 20px;
  position: relative;

  &::after {
    @include pseudo;
    @include size(120px, 4px);
    top: 115px;
    background-color: $background-color-tertiary;
    left: 50%;
    margin-left: -60px;

    @media (min-width: $tablet-width) {
      top: 166px;
    }

    @media (min-width: $desktop-width) {
      top: 211px;
    }
  }

  @media (min-width: $tablet-width) {
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 106px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 190px;
    padding-right: 190px;
    padding-bottom: 148px;
  }
}

.advantages__title {
  color: $color-primary-dark;
  padding-top: 23px;
  padding-bottom: 43px;
  max-width: 320px;
  margin: 0 auto;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding-top: 70px;
    padding-bottom: 89px;
    max-width: inherit;
  }

  @media (min-width: $desktop-width) {
    padding-top: 118px;
    padding-bottom: 118px;
  }
}

.advantages__slider {
  margin-left: 20px;

  @media (min-width: $tablet-width) {
    margin-left: 0;
  }
}

.advantages__slider-list {
  @media (min-width: $tablet-width) {
    justify-content: space-between;
  }
}

.advantages__slider-item {
  @include font(12px, 24px);
  flex: 0 0 63%;
  max-width: 63%;
  color: $color-primary;
  background-repeat: no-repeat;
  background-position: bottom;
  text-align: center;
  padding-right: 10px;
  max-height: 276px;

  @media (min-width: $tablet-width) {
    @include font(14px, 24px);
    flex: 0 0 32.2%;
    max-width: 32.2%;
    max-height: 420px;
    padding-left: 0;
    padding-right: 0;
  }

  @media (min-width: $desktop-width) {
    max-height: 600px;
    font-size: 16px;
  }
}

@media (min-width: $tablet-width) {
  .advantages__slider-item:first-child {
    order: 2;
  }

  .advantages__slider-item:nth-child(2) {
    order: 1;
  }

  .advantages__slider-item:nth-child(3) {
    order: 3;
  }
}

.advantages__slider-content {
  background-color: $background-color-primary;
  height: inherit;
  padding-top: 14px;
  box-sizing: border-box;
  position: relative;

  @media (min-width: $tablet-width) {
    padding-top: 45px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 61px;
  }
}

.advantages__note {
  @include font(24px, 28px);
  font-family: $font-family-secondary;
  display: block;
  text-transform: uppercase;
  color: $color-secondary;
  max-width: 200px;
  margin: 8px auto 34px;

  @media (min-width: $tablet-width) {
    @include font(30px, 35px);
    margin-top: 15px;
    margin-bottom: 47px;
  }

  @media (min-width: $desktop-width) {
    @include font(42px, 49px);
    margin-top: 31px;
    max-width: initial;
    margin-bottom: 68px;
    padding: 0 37px;
  }
}

.advantages__img {
  width: 100%;
}

.advantages__button {
  width: 130px;
  position: absolute;
  left: 50%;
  top: 121px;
  margin-left: -65px;

  @media (min-width: $tablet-width) {
    width: 170px;
    margin-left: -85px;
    top: 182px;
  }

  @media (min-width: $desktop-width) {
    top: 260px;
    width: 200px;
    margin-left: -100px;
  }
}

.advantages__slider {
  .slider__control {
    display: none;
  }
}
