.request {
  display: flex;
  align-items: center;
}

.request__button {
  height: 30px;
  line-height: 30px;
  padding-left: 25px;
  padding-right: 25px;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    height: 40px;
    line-height: 40px;
    padding-left: 37px;
    padding-right: 37px;
    font-size: 18px;
  }

  @media (min-width: $desktop-width) {
    height: 60px;
    line-height: 60px;
  }
}

.request__note {
  @include font(12px, 16px);

  @media (min-width: $tablet-width) {
    margin-left: 20px;
  }

  @media (min-width: $desktop-width) {
    @include font(14px, 20px);
  }
}
