.page-header {
  color: $color-primary;
  padding-bottom: 17px;

  @media (min-width: $tablet-width) {
    padding-top: 3px;
  }
}

.page-header__contacts {
  display: none;

  @media (min-width: $tablet-width) {
    @include font(12px, 14px);
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding-left: 20px;
    padding-right: 20px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 180px;
    padding-right: 180px;
  }
}

.page-header__navigation {
  text-align: center;
  padding-top: 20px;
  position: relative;

  @media (min-width: $tablet-width) {
    text-align: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 0;
  }

  @media (min-width: $desktop-width) {
    padding-left: 180px;
    padding-right: 180px;
  }
}

.page-header__logo {
  @media (min-width: $tablet-width) {
    padding-top: 18px;
  }
}

.page-header__icon-phone {
  position: absolute;
  top: 0;
  right: 0;
  margin: 31px;
}

.page-header-nav__list {
  @media (min-width: $tablet-width) {
    display: flex;
    width: 324px;
    justify-content: space-evenly;
  }

  @media (min-width: $desktop-width) {
    width: 375px;
    justify-content: start;
  }
}

.page-header-nav__item {
  @media (min-width: $desktop-width) {
    margin-right: 25px;
  }
}

.page-header__button {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    font-size: 16px;
    width: 170px;
    margin-top: 19px;
  }

  @media (min-width: $desktop-width) {
    width: 190px;
  }
}

.page-header__user-nav-link {
  @media (min-width: $desktop-width) {
    margin-left: 161px;
    margin-right: 127px;
  }
}
