.main-nav__toggle {
  @include size(40px, 40px);
  box-sizing: content-box;
  margin: 21px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: block;
  background-color: transparent;
  cursor: pointer;
  border: none;
  font-size: 0;
  padding: 0;

  &:hover,
  &:active {
    background-color: transparent;
  }

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  @include pseudo;
  top: 50%;
  left: 50%;
  margin-left: -8px;
  margin-top: -10px;

  @include size(16px, 2px);
  background-color: $background-color-primary-light;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
}

.main-nav--closed .main-nav__wrapper {
  display: none;

  @media (min-width: $tablet-width) {
    display: flex;
  }
}

.main-nav--opened .main-nav__list {
  display: block;
}

.main-nav__list {
  @include ul-reset;
}

.main-nav__link {
  @include font(16px, 44px);
  color: $color-primary;
  text-decoration: none;
  text-transform: uppercase;
  font-family: $font-family-secondary;

  &:hover {
    text-decoration: underline;
    cursor: pointer;
  }

  &:active,
  &:focus {
    text-decoration: underline;
  }

  @media (min-width: $tablet-width) {
    line-height: 34px;
  }
}

.main-nav__wrapper {
  margin-top: 29px;

  @media (min-width: $tablet-width) {
    margin-top: 23px;
  }
}

.main-nav--opened {
  background-color: $background-color-primary;
  height: 100vh;
}

.main-nav__user-nav-link {
  display: block;
  margin-top: 12px;
  margin-bottom: 24px;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav__phone {
  display: block;
  margin-bottom: 7px;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav__copyright {
  @include font(10px, 12px);
  max-width: 165px;
  margin: 41px auto 0;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav__copyright-year {
  display: block;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  top: -1px;

  &::before,
  &::after {
    @include pseudo;
    left: 12px;

    @include size(16px, 2px);
    background-color: $background-color-primary-light;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
}

.main-nav__item--active {
  .main-nav__link {
    color: $color-tertiary;
    position: relative;

    &::after {
      @include size(6px, 6px);
      @include pseudo;
      top: 0;
      right: 0;
      left: 0;
      bottom: -40px;
      margin: auto;
      background-color: $background-color-secondary;
      border-radius: 50%;
    }
  }
}
