.partners {
  background-color: $background-color-primary-light;
  padding: 20px 20px 17px;

  @media (min-width: $tablet-width) {
    padding-bottom: 81px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 180px;
    padding-right: 180px;
    padding-bottom: 113px;
  }
}

.partners__title {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 28px;
  padding-bottom: 46px;
  position: relative;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding-top: 83px;
    padding-bottom: 90px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 111px;
    padding-bottom: 107px;
  }

  &::after {
    @include pseudo($content: "", $position: absolute, $display: block);
    @include size(120px, 4px);
    top: 68px;
    background-color: $background-color-tertiary;
    left: 50%;
    margin-left: -60px;

    @media (min-width: $tablet-width) {
      top: 153px;
    }

    @media (min-width: $desktop-width) {
      top: 182px;
    }
  }
}

.partners__list {
  @include ul-reset;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  max-width: 320px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    max-width: 544px;
  }

  @media (min-width: $desktop-width) {
    flex-wrap: nowrap;
    max-width: inherit;
  }
}

.partners__item {
  width: 50%;
  text-align: center;
  opacity: 0.5;
  margin-bottom: 25px;
  margin-top: 10px;

  @media (min-width: $tablet-width) {
    @include size(33.3%, 95px);
    line-height: 95px;
    filter: grayscale(100%);
    transition: all 0.6s;
    margin-bottom: 31px;
    margin-top: 31px;

    &:hover,
    &:active {
      filter: none;
      opacity: 1;
      cursor: pointer;
    }
  }
}

.partners__image {
  @media (min-width: $tablet-width) {
    vertical-align: middle;
  }
}
