.service-item {
  background-color: $background-color-primary-light;
  padding-top: 27px;
  padding-left: 20px;
  padding-right: 20px;

  @media (min-width: $tablet-width) {
    padding-top: 39px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 75px;
    padding-left: 180px;
    padding-right: 180px;
  }
}
.service-item__advantages-list {
  @include ul-reset;
  @include font(16px, 20px);
  font-family: $font-family-primary;
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 20px;
  text-align: left;

  @media (min-width: $tablet-width) {
    padding-left: 0;
    padding-right: 0;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.service-item__advantages-item {
  display: flex;
  margin-top: 16px;
  margin-bottom: 23px;
}

.service-item__advantages-content {
  max-width: 145px;
  margin-left: 30px;
  padding: 2px 0;
}

.advantages-content__list {
  @include ul-reset;
  @include font(16px, 24px);
  color: $color-secondary;
  margin-top: 8px;

  li {
    position: relative;
    padding-left: 10px;

    &::after {
      @include pseudo;
      @include size(5px, 5px);
      content: "-";
      top: 0;
      left: 0;
    }
  }
}

.service-item__advantages-list--blue {
  background-color: $background-color-primary;
  color: $color-primary;
  padding-top: 28px;
  padding-bottom: 28px;
  height: 100%;
  box-sizing: border-box;

  .service-item__advantages-item:first-child .service-item__advantages-content {
    max-width: 140px;

    @media (min-width: $desktop-width) {
      max-width: 100%;
    }
  }

  .service-item__advantages-item:last-child .service-item__advantages-content {
    max-width: 165px;

    @media (min-width: $desktop-width) {
      max-width: 270px;
    }
  }

  @media (min-width: $tablet-width) {
    padding-left: 40px;
    padding-right: 40px;
    justify-content: center;
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;
    padding-left: 70px;
    padding-right: 70px;
  }
}

.service-item__description {
  @include font(16px, 24px);
  padding-bottom: 40px;
  margin-top: 27px;

  @media (min-width: $desktop-width) {
    max-width: 760px;
    margin: 0 auto;
  }

  p {
    margin: 25px 0;
  }
}

.service-image {
  display: block;
  @include size(100%, 100%);
}

.service-image--main {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  }
}

.service-item__advantages-wrapper {
  display: flex;
  flex-direction: column;
  margin-left: -20px;
  margin-right: -20px;
  margin-bottom: 38px;
  text-align: center;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 47px;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 75px;
  }
}

.service-item__advantages-inner {

  @media (min-width: $tablet-width) {
    width: 50%;
    text-align: left;
  }
}

.service-item__advantages-image {

  @media (min-width: $tablet-width) {
    width: 50%;
  }
}

.service-item__advantages-image--main {
  @media (min-width: $tablet-width) {
    @include size(351px, 630px);
  }

  @media (min-width: $desktop-width) {
    width: 530px;
  }
}

.service-item__title {
  text-align: left;
  margin-top: 0;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
  margin-bottom: 38px;

  @media (min-width: $tablet-width) {
    margin-bottom: 47px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 99px;
  }

  &::after {
    @include pseudo;
    @include size(120px, 4px);
    background-color: $background-color-tertiary;
    bottom: -19px;
    left: 20px;

    @media (min-width: $tablet-width) {
      left: 0;
      bottom: -26px;
    }

    @media (min-width: $desktop-width) {
      bottom: -38px;
    }
  }

  @media (min-width: $tablet-width) {
    margin-left: 0;
    padding-left: 0;
    padding-right: 0;
  }
}

.service-item__terminals-list {
  display: none;

  @media (min-width: $tablet-width) {
    @include ul-reset;
    display: flex;
    flex-direction: column;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    justify-content: space-between;
  }
}
.service-item__terminals-title {
  @include font(20px, 24px);
  color: $color-fourth;
  font-weight: bold;
  display: flex;

  svg {
    margin-right: 30px;
  }
}

.service-item__terminals-item {
  max-width: 500px;

  @media (min-width: $tablet-width) {
    margin-top: 50px;
  }

  @media (min-width: $desktop-width) {
    width: 45%;
  }
}

.service-item__terminals-content {
  padding-left: 70px;

  @media (min-width: $desktop-width) {
    padding-left: 0;
  }
}

.service-item__advantages-button {
  margin: 0 auto;

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.service-item__advantages-button-note {
  display: none;

  @media (min-width: $desktop-width) {
    display: inline-block;
    max-width: 155px;
  }
}

.service-item__advantages-button-wrapper {
  margin-top: 45px;

  @media (min-width: $desktop-width) {
    margin-top: 59px;
  }
}
