body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: $font-family-primary;
  font-size: 12px;
  line-height: 18px;
  font-style: normal;
  color: $color-primary-dark;
  background-color: $background-color-primary;
  overflow-x: hidden;
  background-image: url("../img/bg-blue@1x.png");
  background-repeat: no-repeat;
  background-size: cover;
}

@media (min-width: 768px) {
  body {
    font-size: 14px;
    line-height: 24px;
  }
}

@media (min-width: 1440px) {
  body {
    font-size: 16px;
    line-height: 24px;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  @include visually-hidden;
}



body{
  display: flex;
  flex-direction: column;
  //height: 100%;
}

.page-main{
  /*Основной раздел будет занимать все свободное место на странице*/
  flex: 1 0 auto;
}

footer{
  /*Футер будет занимать столько места по вертикали, сколько и должен и не пикселя больше */
  flex: 0 0 auto;
}

html, body {
  background: #14386E;/*#001c3f;*/
  width: 100%;
  //height: 100%;
  margin: 0;
  border: 0;
  /*overflow: hidden; /*  Disable scrollbars */
  display: block;  /* No floating content on sides */
}

