#bg {
  -webkit-filter: blur(35px);
  filter: blur(35px);
  width: 100%;
  height: 100%;
  position: absolute;
}

.wrapper {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
}

html {
  height: 100%;
}

body {
  height: 100%;
}


.inner {
  position: relative;
  z-index: 3;
}

.hidden-xs {
  display: none;

  @media (min-width: $tablet-width) {
    display: inline-block;
  }
}

.hidden-sm {
  @media (min-width: $tablet-width) {
    display: none;
  }
}
