.cases {
  color: $color-primary;
  padding-top: 20px;
  padding-bottom: 58px;

  @media (min-width: $tablet-width) {
    padding-bottom: 85px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 82px;
  }
}

.cases__slider {
  .slider__wrapper {
    margin-left: 20px;

    @media (min-width: $desktop-width) {
      margin-left: 183px;
    }
  }
}

.cases__list {
  @include ul-reset;
  flex-direction: row;
  align-items: stretch;
  margin-left: 20px;
}

.cases__title {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 30px;
  padding-bottom: 62px;
  position: relative;
  text-align: center;

  &::after {
    @include pseudo;
    @include size(120px, 4px);
    top: 72px;
    background-color: #20B2FF;
    left: 50%;
    margin-left: -60px;

    @media (min-width: $tablet-width) {
      top: 136px;
    }
  }

  @media (min-width: $tablet-width) {
    padding-top: 60px;
    padding-bottom: 84px;
  }
}

.cases__slider-item {
  flex: 0 0 76%;
  max-width: 76%;
  padding-right: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  &:hover {
    cursor: pointer;
  }

  @media (min-width: $tablet-width) {
    flex: 0 0 46%; /*Артефакт пропадает при ширине 50% и больше или когда слайдов 4 шт (четное мб?)*/
    max-width: 46%;
    padding-right: 22px;
  }

  @media (min-width: $desktop-width) {
    flex: 0 0 43%;
    max-width: 43%;
    padding-right: 30px;
  }
}

.cases__slider-item:hover .cases__button {
  cursor: pointer;
  background-color: $background-color-secondary;
  border: 1px solid $border-color-tertiary;
  color: $color-primary-dark;
}

.cases__picture-wrapper {
  position: relative;
}

.cases__stats {
  display: none;
  opacity: 0;

  @media (min-width: $desktop-width) {
    @include ul-reset;
    transition: all 0.6s;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: $background-color-secondary;
    color: $color-primary-dark;
    padding: 20px;
    box-sizing: border-box;
  }
}

.cases__button {
  width: 130px;
  margin-top: 20px;

  &:hover,
  &:active {
    cursor: pointer;
    background-color: $background-color-secondary;
    border: 1px solid $border-color-tertiary;
    color: $color-primary-dark;
  }

  @media (min-width: $tablet-width) {
    width: 150px;
    font-size: 16px;
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    margin-top: 20px;
  }
}

.cases__slider-list {
  counter-reset: list 0;
}

.cases__slider-item::before {
  @include font(32px, 28px);
  counter-increment: list;
  content: counter(list);
  font-family: $font-family-secondary;

  @media (min-width: $tablet-width) {
    @include font(42px, 44px);
  }
}

.cases__slider-item:hover::before,
.cases__slider-item:active::before {
  color: $color-tertiary;
}

.cases__desc {
  min-height: 160px;
  margin: 23px 0;

  @media (min-width: $desktop-width) {
    margin: 31px 0 0;
  }
}

.cases__slider-control.slider__control {
  @media (min-width: $tablet-width) {
    display: block;
  }
}

.cases__img {
  width: 100%;
  object-fit: cover;
  display: block;

  @media (min-width: $tablet-width) {
    height: 240px;
  }

  @media (min-width: $desktop-width) {
    height: 380px;
  }
}

@media (min-width: $desktop-width) {
  .cases__slider-item:hover .cases__stats {
    opacity: 1;
    display: block;
  }
}

.cases__slider-wrapper {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  flex: 1 0 auto;

  .title {
    text-align: left;
    @include font(16px, 24px);
    flex: 1 0 auto;
    margin: 21px auto;

    @media (min-width: $tablet-width) {
      @include font(22px, 28px);
      max-width: 260px;
      margin: 14px 0 22px;
    }

    @media (min-width: $desktop-width) {
      margin: 11px 0 34px;
      max-width: 310px;
    }
  }
}




