.user-nav-link {
  color: $color-tertiary;
  text-decoration: none;

  @media (min-width: $tablet-width) {
    @include font(12px, 14px);
  }
}

.user-nav-link__title {
  text-decoration: underline;

  &:hover {
    text-decoration: none;
    cursor: pointer;
  }

  &:active,
  &:focus {
    text-decoration: none;
  }
}
