.services {
  color: $color-primary;

  .title {
    @include font(26px, 32px);

    @media (min-width: $tablet-width) {
      @include font(54px, 70px);
    }

    @media (min-width: $desktop-width) {
      @include font(68px, 80px);
    }
  }

  @media (min-width: $tablet-width) {
    text-align: center;
  }
}

.services__title {
  max-width: 240px;
  margin: 13px auto 18px;
  text-align: center;

  @media (min-width: $tablet-width) {
    max-width: 768px;
    margin-top: 39px;
    margin-bottom: 35px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 54px;
    margin-bottom: 53px;
  }
}

.services__button-wrapper {
  //display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 33px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: center;
    margin-bottom: 66px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 81px;
  }
}

.services__button {
  margin-bottom: 10px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}

.services__button-note {
  @media (min-width: $tablet-width) {
    max-width: 82px;
  }

  @media (min-width: $desktop-width) {
    max-width: 115px;
  }
}

/*Слайдер*/

.services__slider-content {
  min-height: 162px;
  display: flex;
  align-items: flex-end;

  @media (min-width: $tablet-width) {
    min-height: 202px;
  }

  @media (min-width: $desktop-width) {
    min-height: 380px;
  }
}

.services__slider-item {
  flex: 0 0 38%;
  max-width: 38%;

  &:hover {
    cursor: pointer;
  }

  @media (min-width: $tablet-width) {
    flex: 0 0 20%;
    max-width: 20%;
  }
}

.services__img {
  width: 100%;
  min-height: 156px;
  display: inherit;
  background-blend-mode: color, soft-light, normal;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;

  &::after {
    @include pseudo;
    @include size(100%, 100%);
    top: 0;
    left: 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-blend-mode: normal, soft-light, normal;
    opacity: 0;
    transition: all 0.6s;
    transform: scale(1.1);
    z-index: 1;
  }

  &:hover::after {
    opacity: 1;
  }

  @media (min-width: $tablet-width) {
    min-height: 195px;
  }

  @media (min-width: $desktop-width) {
    min-height: 366px;
  }
}

