  }
}

.services__slider-link:hover .services__slider-title {
  color: $color-tertiary;
}

.services__slider-title {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  min-height: 42px;
  z-index: 1;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    min-height: 47px;
  }

  @media (min-width: $desktop-width) {
    min-height: 67px;
  }
}

.services__desc {
  @include font(14px, 24px);
  padding: 0 39px;
  font-family: $font-family-primary;
  text-align: center;
  margin-bottom: 45px;
  color: $color-primary;

  @media (min-width: $tablet-width) {
